
@import "../../style/mixins/mixin";
@import "../../style/variables";
html{
  font-size: 10px;
}
.diagram{
  width: 15rem;
  height: 15rem;
  fill: #BC9CFF;
}
.diagram__wrapper{
  display: flex;
}
.diagram__quantity{
  text-align: center;
  -moz-transform: translateY(0.1em);
  -ms-transform: translateY(0.1em);
  -webkit-transform: translateY(0.1em);
  transform: translateY(0.1em);

}
.diagram__number{
  @include fontContent(.6em,2);
  text-anchor: middle;
  font-weight: $bold-weight;
  -moz-transform: translateY(-0.25em);
  -ms-transform: translateY(-0.25em);
  -webkit-transform: translateY(-0.25em);
  transform: translateY(-0.25em);
}
.diagram__text {
  @include fontContent(.32em,2);
  text-transform: uppercase;
  text-anchor: middle;
  font-weight: $bold-weight;
  -moz-transform: translateY(1.2em);
  -ms-transform: translateY(1.2em);
  -webkit-transform: translateY(1.2em);
  transform: translateY(1.2em);
}
.diagram__legend{
  margin-top: 4.5rem;
  margin-left: 1.5rem;
  list-style: none;
  padding-left: 0;
  color: $lighter-dark;
  @include fontContent(1.4rem,1.5rem);

}
.diagram__color{
  position: relative;
  margin-bottom: 1rem;
  width: 80%;
}
.diagram__color:before{
  content: "";
  position: absolute;
  top: .6rem;
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  transform: translateY(-50%);
  margin-right: 2rem;
}
.diagram__color-text{
  padding-left: 1.5rem;
  margin-top: 2rem;

}
.diagram__legend>:nth-child(1):before{
background-image: linear-gradient(180deg, #FFE39C 0%, #FFBA9C 100%);
}
.diagram__legend>:nth-child(2):before{
  background-image: $gradient-green;
}
.diagram__legend>:nth-child(3):before{
  background-image: $gradient-purple;
}
.diagram__legend>:nth-child(4):before{
  background-image: linear-gradient(180deg, #919191 0%, #3D4975 100%);
}